/*--------------------------------------------------------------
## Setup
--------------------------------------------------------------*/
html {
  height: 100%;
  font-size: 16px; }
  @media only screen and (max-width: 768px) {
    html {
      font-size: 14px; } }
  @media only screen and (max-width: 390px) {
    html {
      font-size: 14px; } }

body {
  height: 100%;
  margin: 0; }

* {
  box-sizing: border-box; }

h1, h2, h3, h4, h5 {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: normal; }

ul {
  padding: 0;
  margin: 0;
  list-style: none; }

p {
  margin: 0; }

/*--------------------------------------------------------------
## Layout
--------------------------------------------------------------*/
.wrapper {
  min-height: 100%;
  position: relative;
  overflow: hidden; }

/*--------------------------------------------------------------
## timeline
--------------------------------------------------------------*/
.timeline {
  position: relative;
  color: #fff;
  font-weight: 300;
  background-color: #16181a;
  overflow: hidden; }
  @media only screen and (max-width: 1024px) {
    .timeline {
      color: #16181a;
      background-color: #fff; } }
  .timeline__title {
    margin-top: 80px;
    text-align: center; }
  .timeline__titletext {
    font-size: 36px;
    font-weight: 500;
    line-height: 1; }
  .timeline__container {
    height: 90vh;
    font-size: 16px;
    font-size: 1rem;
    position: relative;
    max-width: 775px;
    margin: 40px auto 0 auto;
    padding-bottom: 50px; }
    @media only screen and (max-width: 768px) {
      .timeline__container {
        max-width: 715px; } }
    @media only screen and (max-width: 390px) {
      .timeline__container {
        max-width: 80%; } }
    .timeline__container::before {
      content: '';
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      height: 100%;
      width: 1px;
      background: #5e6266; }
      @media only screen and (max-width: 390px) {
        .timeline__container::before {
          left: 0%; } }
  .timeline__yearblock {
    margin: 0;
    margin-bottom: 34px;
    position: relative; }
    .timeline__yearblock:first-child::after {
      content: '';
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: 38px;
      display: inline-block;
      width: 10px;
      height: 1px;
      background-color: #5e6266; }
      @media only screen and (max-width: 390px) {
        .timeline__yearblock:first-child::after {
          left: 0%; } }
  .timeline__year {
    padding: 7px;
    font-size: 16px;
    font-size: 1rem;
    font-weight: 500;
    display: inline-block;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    position: relative;
    background-color: #16181a; }
    @media only screen and (max-width: 390px) {
      .timeline__year {
        left: 0%; } }
    @media only screen and (max-width: 1024px) {
      .timeline__year {
        color: #16181a;
        background-color: #fff; } }
  .timeline__block {
    padding: 27px 31px 0px 31px;
    position: relative;
    width: 50%; }
    .timeline__block:nth-child(odd) {
      padding-top: 5px; }
      .timeline__block:nth-child(odd)::after {
        top: 14px; }
    @media only screen and (max-width: 390px) {
      .timeline__block {
        width: 100%; }
        .timeline__block:nth-child(odd) {
          padding-top: 27px; }
          .timeline__block:nth-child(odd)::after {
            top: 33px; } }
    .timeline__block::after {
      content: '';
      display: inline-block;
      position: absolute;
      transform: translateX(-50%);
      left: 100%;
      top: 33px;
      background-color: #1097fe;
      width: 10px;
      height: 10px;
      border-radius: 50%; }
      @media only screen and (max-width: 390px) {
        .timeline__block::after {
          left: 0; } }
    .timeline__block_left {
      padding-left: 0;
      text-align: right;
      left: 0; }
      @media only screen and (max-width: 390px) {
        .timeline__block_left {
          text-align: left;
          padding-left: 31px;
          padding-right: 0;
          left: 0%; } }
    .timeline__block_right {
      padding-right: 0;
      left: 50%; }
      .timeline__block_right::after {
        left: 0; }
      @media only screen and (max-width: 390px) {
        .timeline__block_right {
          left: 0%; } }
  @media only screen and (min-width: 1024px) {
    .timeline__content_hidden {
      visibility: hidden; }
    .timeline__content_animation_bounce-in {
      visibility: visible;
      animation: fade-in .6s; } }
  .timeline__text {
    margin-bottom: 10px; }
  .timeline__picture {
    display: inline-block;
    margin-bottom: 10px; }
  .timeline__img {
    max-width: 100%;
    border-radius: 5px; }
  .timeline__footer {
    height: 120px;
    width: 100%;
    position: absolute;
    z-index: 1;
    bottom: 0px; }
  .timeline__gradient {
    display: block;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: linear-gradient(to top, #16181a 0%, #16181a 40%, rgba(22, 24, 26, 0) 99%, rgba(22, 24, 26, 0) 100%); }
    @media only screen and (max-width: 1024px) {
      .timeline__gradient {
        background: linear-gradient(to top, white 0%, white 40%, rgba(255, 255, 255, 0) 99%, rgba(255, 255, 255, 0) 100%); } }
  .timeline__button {
    outline: none;
    width: 185px;
    height: 40px;
    background-color: transparent;
    color: #fff;
    text-transform: uppercase;
    border: 1px solid #fff;
    border-radius: 5px;
    z-index: 2;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 50%;
    cursor: pointer; }
    @media only screen and (max-width: 1024px) {
      .timeline__button {
        color: #16181a;
        border: 1px solid #16181a; } }

/*--------------------------------------------------------------
## preloader
--------------------------------------------------------------*/
.preloader {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: #16181a;
  z-index: 3; }
  .preloader__box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 120px;
    height: 120px; }
  .preloader__load {
    border: 16px solid #1097fe;
    border-top: 4px solid #1097fe;
    border-bottom: 4px solid #1097fe;
    background: #fff;
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 1s linear infinite; }

@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }
